  .awura-rolling-counter{
    &-column{
      gap: 100px;
      display: grid;
      max-width: 500px;
      position: relative;
      grid-template-columns: 1fr 1fr;
      @include max(479px) {
        gap: 24px;
        max-width: 250px;
        grid-template-columns: 1fr;
      }
      &:before{
        content: "";
        left: 47%;
        top: 30px;
        width: 1px;
        height: 100px;
        position: absolute;
        background-color: rgba(255, 255, 255, 0.1);
        @include max(479px) {
          content: none;
        }
      }
    }
    &-value{
      margin-bottom: 20px;
      span{
        font-size: 65px;
        font-weight: 500;
        color: var(--accent-color);
      }
    }
  }
  
  .rolling-counter {
    display: inline-flex;
    overflow: hidden;
    font-size: 65px;
    line-height: 70px;
    height: 60px;
    font-weight: 500;
    letter-spacing: -0.02em;
    color: var(--accent-color);
  }

  .rolling-counter .digit {
    display: flex;
    width: 34px;
    flex-direction: column;
    transition: transform 1s ease-out;
  }

  .rolling-counter .digit span {
    height: 60px;
    display: block;
  }
