.awura-hero{
  &-section{
    position: relative;
    padding: 195px 0 320px;
    background-color: var(--pampas-color);
     @include max(1199px) {
      padding: 160px 0 320px;
    }
     @include max(767px) {
      padding: 130px 0 80px;
    }
  }
  &-content{
    text-align: center;
    max-width: 1000px;
    margin: 0 auto;
    @include max(1399px) {
      max-width: 830px;
    }
    @include max(1199px) {
      max-width: 700px;
    }
    h1{
      margin-bottom: 20px;
    }
    p{
      max-width: 840px;
      margin: 0 auto;
    }
  }
}