.awura-section-padding{
  padding: 130px 0;
  @include max(991px) {
    padding: 100px 0;
  }
  @include max(991px) {
    padding: 80px 0;
  }
}

.awura-section-pb{
  padding-bottom: 130px;
  @include max(991px) {
    padding-bottom: 100px;
  }
  @include max(991px) {
    padding-bottom: 80px;
  }
}

.awura-section-pt{
  padding-top: 130px;
  @include max(991px) {
    padding-top: 100px;
  }
  @include max(991px) {
    padding-top: 80px;
  }
}

.awura-mt50{
  margin-top: 50px;
  @include max(991px) {
    margin-top: 35px;
  }
}

.awura-p-lg{
  p{
    font-size: 20px;
    line-height: 30px;
    @include max(575px) {
      font-size: 18px;
    }
   }
}

.mb24{
  display: block;
  margin-bottom: 24px;
}

.awura-section-title{
  padding-bottom: 80px;
  @include max(991px) {
    padding-bottom: 60px;
  }
  &.center-align{
    max-width: 650px;
    margin: 0 auto;
    text-align: center;
  }
}

.awura-default{
  &-thumb{
    @include max(991px) {
      margin-bottom: 40px;
    }
    img{
      width: 100%;
      height: 100%;
      border-radius: 20px;
      object-fit: cover;
      @include max(767px) {
        border-radius: 10px;
      }
    }
  }
  &-content{
    h2{
      margin-bottom: 20px;
    }
    p{
      font-size: 20px;
      @include max(767px) {
        font-size: 18px;
      }
    }
    &.light-color{
      h2, p{
        color: var(--white-color);
      }
    }
  }
  
}


.awura-position-sticky{
  top: 100px;
  position: sticky;
  @include max(991px) {
    position: static;
  }
}

.position-relative{
  position: relative;
  z-index: 0;
}