.awura-about-content-box{
  padding: 40px;
  border-radius: 20px;
  background-color: var(--accent-color);
  @include max(1399px) {
    padding: 30px;
  }
  h3{
    margin-bottom: 30px;
    color: var(--white-color);
  }
}

.awura-iconbox{
  &-wrap{
    gap: 24px;
    display: flex;
    flex-direction: column;
    @include max(991px) {
      margin-top: 40px;
    }
  }
  &-box{
    padding: 30px;
    border-radius: 10px;
    background-color: var(--white-color);
  }
  &-icon{
    width: 60px;
    height: 60px;
    display: flex;
    margin-bottom: 30px;
    border-radius: 100px;
    align-items: center;
    justify-content: center;
    background-color: var(--pampas-color);
  }
  &-data{
    h3{
      margin-bottom: 15px;
    }
    p{
      margin: 0;
    }
  }
}

.iconbox-sticky{
  top: 100px;
  position: sticky;
  @include max(991px) {
    position: static;
  }
}