.awura-video{
  &-section{
    z-index: 0;
    position: relative;
    margin-top: -240px;
    padding-bottom: 130px;
    @include max(991px) {
      padding-bottom: 100px;
    }
    @include max(767px) {
      padding: 80px 0;
      margin: 0;
    }
  }
  &-wrapper{
    position: relative;
    border-radius: 20px;
    overflow: hidden;
    margin: 0 auto;
    transition: width 1.2s ease;
    @include max(767px) {
      border-radius: 10px;
    }
    @include max(767px) {
        height: 250px;
      }
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      
    }
 
  }
}

.awura-video-popup{
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  position: absolute;
  img{
      @include max(767px) {
        height: 80px;
      }
    }
}

@keyframes waves {
  0% {
    -webkit-transform: scale(0.2, 0.2);
    transform: scale(0.2, 0.2);
    opacity: 0;
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
  }
  50% {
    opacity: 0.9;
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=90)";
  }
  100% {
    -webkit-transform: scale(0.9, 0.9);
    transform: scale(0.9, 0.9);
    opacity: 0;
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
  }
}



.waves {
  position: absolute;
  width: 190px;
  height: 190px;
  background: rgba(255, 255, 255, 0.5);
  opacity: 0;
  border-radius: 100%;
  right: -45px;
  bottom: -45px;
  z-index: -1;
   -webkit-animation: waves 3s ease-in-out infinite;
  animation: waves 3s ease-in-out infinite;
  @include max(767px) {
    right: -55px;
    bottom: -55px;
  }
}
        
.wave-1 {
  -webkit-animation-delay: 0s;
  animation-delay: 0s;
}

.wave-2 {
  -webkit-animation-delay: 1s;
  animation-delay: 1s;
}

.wave-3 {
  -webkit-animation-delay: 2s;
  animation-delay: 2s;
}
